.App {
  text-align: center;
}



.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}
@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); }
}
@keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

@-webkit-keyframes spinner {
  0% { 
     -webkit-transform: rotate3d(1, 1, 1, 20deg);
     -ms-transform: rotate3d(1, 1, 1, 0deg);
     transform: rotate3d(1, 1, 1, 0deg);
     -webkit-transform: scale(0.5);
             transform: scale(0.5)
  }
  50% { 
     -webkit-transform: rotate3d(1, 1, 1, 18deg);
     -ms-transform: rotate3d(1, 1, 1, 40deg);
     transform: rotate3d(1, 1, 1, 90deg);
     -webkit-transform: scale(1.2);
             transform: scale(1.2)
  }
  100% { 
      -webkit-transform: rotate3d(1, 1, 1, 10deg);
     -ms-transform: rotate3d(1, 1, 1, 60deg);
     transform: rotate3d(1, 1, 1, 70deg);
     -webkit-transform: scale(2);
             transform: scale(2)

  }
}

@keyframes spinner {
  0% { 
     -webkit-transform: rotate3d(1, 1, 1, 20deg);
     -ms-transform: rotate3d(1, 1, 1, 0deg);
     transform: rotate3d(1, 1, 1, 0deg);
     -webkit-transform: scale(0.5);
             transform: scale(0.5)
  }
  50% { 
     -webkit-transform: rotate3d(1, 1, 1, 18deg);
     -ms-transform: rotate3d(1, 1, 1, 40deg);
     transform: rotate3d(1, 1, 1, 90deg);
     -webkit-transform: scale(1.2);
             transform: scale(1.2)
  }
  100% { 
      -webkit-transform: rotate3d(1, 1, 1, 10deg);
     -ms-transform: rotate3d(1, 1, 1, 60deg);
     transform: rotate3d(1, 1, 1, 70deg);
     -webkit-transform: scale(2);
             transform: scale(2)

  }
}


